var img = "data:image/gif;base64,";

var requestAnimationFrame =
  window.requestAnimationFrame ||
  window.mozRequestAnimationFrame ||
  window.webkitRequestAnimationFrame ||
  window.msRequestAnimationFrame || function(callback) {
    setTimeout(callback, 1000 / 60);
  };

function Kitten(opts) {
  var inc = opts.baseIncrement;
  var w = opts.widthOffset
  var h = opts.heightOffset
  var src = opts.gif;
  var srcWidth = opts.gifWidth;
  var srcHeight = opts.gifHeight;
  var posX = (Math.random() * (window.innerWidth - (w * 2))) + w;
  var posY = (Math.random() * (window.innerHeight - (h * 2))) + h;
  var increment = (Math.random() * inc) + 2;
  var node = document.createElement('img');
  var debugNode = document.createElement('span');
  debugNode.setAttribute('style', 'font-size: 12px;display: block; position: fixed; top: '
    + posY + 'px; left: ' + posX + 'px;z-index: 999999; border: 1px solid red;');
  node.setAttribute('src', src);
  node.setAttribute('style', 'width: ' + srcWidth + 'px; height: ' + srcHeight + 'px;display: block; position: fixed; top: '
    + posY + 'px; left: ' + posX + 'px;z-index: 99999');

  if (Math.random() < 0.5) {
    increment = 0 - increment;
    node.style['-moz-transform'] = 'scaleX(-1)';
    node.style['-o-transform'] = 'scaleX(-1)';
    node.style['-webkit-transform'] = 'scaleX(-1)';
    node.style['transform'] = 'scaleX(-1)';
  }
  node.style['-webkit-filter'] = 'hue-rotate(30deg)';

  function move() {
    if (posX >= (window.innerWidth - 150)) {
      increment = 0 - increment;
      node.style['-moz-transform'] = 'scaleX(-1)';
      node.style['-o-transform'] = 'scaleX(-1)';
      node.style['-webkit-transform'] = 'scaleX(-1)';
      node.style['transform'] = 'scaleX(-1)';
      posX = window.innerWidth - 151;
    } else if (posX <= 0) {
      increment = 0 - increment;
      node.style['-moz-transform'] = '';
      node.style['-o-transform'] = '';
      node.style['-webkit-transform'] = '';
      node.style['transform'] = '';
      posX = 1;
    }
    posX = posX + increment;
    node.style.left = posX + 'px';
    if (opts.debug) {
      debugNode.style.left = posX + 'px';
      debugNode.innerHTML = 'pos : ' + posX.toFixed(2);
    }
  }

  return {
    inject: function() {
      document.body.appendChild(node);
      if (opts.debug) {
        document.body.appendChild(debugNode);
      }
    },
    update: move,
    destroy: function() {
      document.body.removeChild(node);
    }
  };
}

function run(opts) {
  var running = true;
  if (Object.prototype.toString.call(opts) === '[object Number]') {
    opts = { kittens: opts };
  }
  opts = opts || {};
  opts.kittens = opts.kittens || 0;
  opts.baseIncrement = opts.baseIncrement || 5;
  opts.widthOffset = opts.widthOffset || 100;
  opts.heightOffset = opts.heightOffset || 150;
  opts.gif = opts.gif || img;
  opts.gifWidth = opts.gifWidth || 157;
  opts.gifHeight = opts.gifHeight || 109;
  var kittens = [];
  for (var i = 0; i < opts.kittens; i++) {
    var k = Kitten(opts);
    k.inject();
    kittens.push(k);
  }

  function update() {
    kittens.forEach(function(k) {
      k.update();
    });
    if (running) {
      requestAnimationFrame(update);
    }
  }
  requestAnimationFrame(update);
  return {
    remove: function(nbr) {
      nbr = nbr || 1;
      for (var j = 0; j < nbr; j++) {
        var k = kittens.pop();
        if (typeof k !== 'undefined') {
          k.destroy();
        }
      }
    },
    add: function(nbr) {
      nbr = nbr || 1;
      for (var j = 0; j < nbr; j++) {
        var k = Kitten(opts);
        k.inject();
        kittens.push(k);
      }
    },
    stop: function() {
      running = false;
      kittens.forEach(function(k) {
        k.destroy();
      });
    }
  };
}

var NyanCatConf = {
  gif: 'http://vignette3.wikia.nocookie.net/nickfanon/images/6/6b/NyanCat.gif/revision/latest?cb=20120408034008',
  gifWidth: 100,
  gifHeight: 100
};

var PigsConf = {
  gif: 'http://www.chezmaya.com/cartesvirtuelles/queljournee/pig2.gif',
  gifWidth: 100,
  gifHeight: 109,
  kittens: 5
};

module.exports = {
  init: run,
  run: run,
  NyanCat: NyanCatConf,
  PigsConf: PigsConf
};
